html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
  background: var(--background);
  color: var(--text-color);
}

h1, h2, h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 24px;
  margin-bottom: 3px;
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 28px;
  }
}

a,
a:visited,
a:active {
  text-decoration: none;
}

:root {
  --font-inter: "Inter", sans-serif;
}

:root {
  --limegreen: hsl(163, 72%, 41%);
  --brightred: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram-start: hsl(37, 97%, 70%);
  --instagram-middle: hsl(5, 77%, 71%);
  --instagram-end: hsl(329, 70%, 58%);
  --toggle-background-start: hsl(210, 78%, 56%);
  --toggle-background-end: hsl(146, 68%, 55%);
  --toggle: hsl(230, 22%, 74%);
  --dark-blue-bg: hsl(230, 17%, 14%);
  --dark-blue-topbg: hsl(232, 19%, 15%);
  --desaturated-blue-cardbg: hsl(228, 28%, 20%);
  --dark-card-hover: hsl(228, 25%, 27%);
  --desaturated-blue-text: hsl(228, 34%, 66%);
  --white-text: hsl(0, 0%, 100%);
  --light-white-bg: hsl(0, 0%, 100%);
  --light-pale-blue-topbg: hsl(225, 100%, 98%);
  --light-grayish-blue-cardbg: hsl(227, 47%, 96%);
  --light-card-hover: hsl(228, 33%, 91%);
  --grayish-blue-text: hsl(228, 12%, 44%);
  --dark-blue-text: hsl(230, 17%, 14%);
  --background: var( --light-white-bg);
  --text-color: var(--dark-blue-text);
  --text-color2: var(--grayish-blue-text);
  --card-bg: var(--light-grayish-blue-cardbg);
  --card-hover: var(--light-card-hover);
}

@media (prefers-color-scheme: dark) {
  :root {
    --background: var(--dark-blue-bg);
    --text-color: var(--white-text);
    --text-color2: var(--desaturated-blue-text);
    --card-bg: var( --desaturated-blue-cardbg);
    --card-hover: var(--dark-card-hover);
  }
}
body.dark {
  --background: var(--dark-blue-bg);
  --text-color: var(--white-text);
  --text-color2: var(--desaturated-blue-text);
  --card-bg: var( --desaturated-blue-cardbg);
  --card-hover: var(--dark-card-hover);
}

body.light {
  --background: var( --light-white-bg);
  --text-color: var(--dark-blue-text);
  --text-color2: var(--grayish-blue-text);
  --card-bg: var(--light-grayish-blue-cardbg);
  --card-hover: var(--light-card-hover);
}

.container {
  max-width: 1100px;
  padding: 0 25px;
  margin: 0 auto 46px;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
    max-width: 1100px;
    padding: 0 25px;
    margin: 0 auto 46px;
  }
}

.grid-parent {
  display: grid;
  gap: 20px;
}
@media (min-width: ) {
  .grid-parent {
    display: grid;
    grid-template-columns: 1fr;
    gap: 30px;
  }
}
@media (min-width: 40em) {
  .grid-parent {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;
  }
}
@media (min-width: 71.875em) {
  .grid-parent {
    display: grid;
    grid-template-columns: repeat(4, 250px);
    gap: 20px;
  }
}

.grid-parent2 {
  display: grid;
  gap: 20px;
}
@media (min-width: ) {
  .grid-parent2 {
    display: grid;
    grid-template-columns: 1fr;
    gap: 30px;
  }
}
@media (min-width: 40em) {
  .grid-parent2 {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;
  }
}
@media (min-width: 71.875em) {
  .grid-parent2 {
    display: grid;
    grid-template-columns: repeat(4, 250px);
    gap: 20px;
  }
}

.header {
  margin-top: 36px;
  margin-bottom: 46px;
  display: flex;
  flex-wrap: wrap;
}
@media (min-width: 40em) {
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.header__subtitle {
  font-size: 14px;
  font-weight: 700;
  color: var(--text-color2);
}
.header__toggle {
  margin-left: auto;
}

.toggle {
  display: grid;
  border: none;
  grid-template-columns: 1fr 1fr;
  gap: 4px 8px;
  /*
      #system:checked ~ .toggle__button {
        left: 50%;
        right: auto;
        transform: translate(-50%);
      }
  */
}
.toggle label {
  font-size: 14px;
  font-weight: 700;
  color: var(--text-color);
}
.toggle label[for=dark] {
  grid-column: 1/2;
  justify-self: end;
}
.toggle label[for=light] {
  grid-column: 3/4;
}
.toggle__wrapper {
  position: relative;
  grid-column: 2/3;
  grid-row: 1/2;
  height: 24px;
  border-radius: 12px;
}
.toggle input[type=radio] {
  margin: 0 -1px 0 0;
  width: 22px;
  height: 22px;
  accent-color: #3DED97;
  /* &:focus ~ .toggle__button {
     border: 2px solid white;
   }*/
}
.toggle__background {
  position: absolute;
  display: block;
  height: 100%;
  border-radius: 12px;
  width: 100%;
  top: 0;
  background: linear-gradient(225deg, var(--toggle-background-end) 0%, var(--toggle-background-start) 98.02%);
  pointer-events: none;
}
.toggle__button {
  position: absolute;
  width: 18px;
  height: 18px;
  left: 2px;
  top: 2px;
  right: 100%;
  border-radius: 50%;
  background-color: var(--toggle);
  transition: all 150ms ease-in-out;
}
.toggle #dark:checked ~ .toggle__button {
  left: 5%;
  right: 3px;
}
.toggle #light:checked ~ .toggle__button {
  left: 55%;
  right: auto;
  transform: translate(-5%);
}

.card {
  position: relative;
  overflow: hidden;
  background: var(--card-bg);
  color: var(--text-color);
  text-align: center;
  padding: 25px;
  border-radius: 5px;
  transition: background 150ms ease-in-out;
  cursor: pointer;
}
.card:hover {
  background: var(--card-hover);
}
.card--facebook {
  border-top: 5px solid var(--facebook);
}
.card--twitter {
  border-top: 5px solid var(--twitter);
}
.card--instagram::before {
  content: "";
  position: absolute;
  display: block;
  left: 0;
  top: 0;
  height: 5px;
  width: 100%;
  background: linear-gradient(225deg, var(--instagram-start) 20%, var(--instagram-end) 80.02%);
}
.card--youtube {
  border-top: 5px solid var(--brightred);
}
.card__platform {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 5px;
  margin-bottom: 28px;
}
.card__icon {
  margin-right: 8px;
}
.card__username {
  font-size: 12px;
  font-weight: 700;
  margin-left: 8px;
}
.card__followers {
  margin-bottom: 25px;
}
.card__count {
  color: var(--text-color);
  font-weight: 700;
  letter-spacing: -2px;
  line-height: 1;
  margin-bottom: 5px;
}
.card__count--big {
  font-size: 56px;
}
.card__count--small {
  font-size: 56px;
}
.card__label {
  color: var(--text-color);
  font-size: 12px;
  letter-spacing: 4px;
  font-weight: 400;
  text-transform: uppercase;
}
.card__change {
  display: flex;
  font-size: 12px;
  font-weight: 700;
  align-items: center;
  justify-content: center;
}
.card__change--up {
  color: var(--limegreen);
}
.card__change--down {
  color: var(--brightred);
}
.card__change img {
  margin-right: 4px;
}
.card h2 {
  font-size: 24px;
  font-weight: 700;
}
.card--facebook1 {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
}
.card__sub {
  font-size: 14px;
  font-weight: 700;
  justify-self: start;
  text-transform: capitalize;
}
.card__icon1 {
  justify-self: end;
}
.card__number1 {
  font-size: 32px;
  justify-self: start;
}
.card__number1--up {
  color: var(--limegreen);
  justify-self: end;
  align-self: end;
}
.card__number1--down {
  color: var(--brightred);
  justify-self: end;
  align-self: end;
}

.footer {
  padding: 25px;
  text-align: center;
  font-weight: 700;
}

.footer a {
  font-size: 14px;
  color: #3e52a3;
  font-weight: 700;
}